<template>
  <div class="about">
    <Canvas :width="canvas.width" :height="canvas.height" :camera="canvas.camera" />
  </div>
</template>
<script lang="ts">
import {
  reactive, onMounted, ref, onUnmounted,
} from 'vue';
import { useEvent } from 'vue-composable';
import { animate } from '../three/instance';
import Canvas from '../components/Canvas.vue';

import vehicle from '../three/layers/vehicle';

const CANVAS_WIDTH_PADDING = 0;
const CANVAS_HEIGHT_PADDING = 0;

export default {
  components: {
    Canvas,
  },
  setup() {
    const canvas = reactive({
      width: 0,
      height: 0,
      camera: 'default',
    });
    const stopAnimate = ref();
    onMounted(() => {
      canvas.width = window.innerWidth - CANVAS_WIDTH_PADDING;
      canvas.height = window.innerHeight - CANVAS_HEIGHT_PADDING;
      stopAnimate.value = animate();
      vehicle.load();
    });

    onUnmounted(() => {
      if (stopAnimate.value) {
        stopAnimate.value();
      }
    });

    useEvent(window, 'resize', () => {
      canvas.width = window.innerWidth - CANVAS_WIDTH_PADDING;
      canvas.height = window.innerHeight - CANVAS_HEIGHT_PADDING;
      console.log('-----------world size: ');
      console.log(canvas.width);
      console.log(canvas.height);
    });
    return {
      canvas,
    };
  },
};
</script>
